import { API_BASE_URL } from '../config/api';
import authService from './authService';
import logger from '../utils/logger';

// API ì‘ë‹µ íƒ€ì… ì •ì˜
export interface APIResponse<T = any> {
  data?: T;
  error?: string;
  message?: string;
}

// ê¸°ë³¸ API í´ë¼ì´ì–¸íŠ¸
class APIClient {
  private baseURL: string;

  constructor(baseURL: string) {
    this.baseURL = baseURL;
  }

  private async request<T>(
    endpoint: string, 
    options: RequestInit = {}
  ): Promise<APIResponse<T>> {
    try {
      const headers: Record<string, string> = {
        'Content-Type': 'application/json',
        ...(options.headers as Record<string, string>),
      };
      
      // JWT ì¸ì¦ í† í° ì¶”ê°€
      const token = authService.getAccessToken();
      if (token) {
        headers.Authorization = `Bearer ${token}`;
      }

      const response = await fetch(`${this.baseURL}${endpoint}`, {
        credentials: 'include',
        ...options,
        headers,
      });

      const data = await response.json();

      if (!response.ok) {
        // 401 ì˜¤ë¥˜ ì‹œ ì¸ì¦ ì„œë¹„ìŠ¤ë¥¼ í†µí•œ ìë™ í† í° ìƒˆë¡œê³ ì¹¨
        if (response.status === 401) {
          const refreshResult = await authService.refreshAccessToken();
          if (refreshResult.success) {
            // ìƒˆ í† í°ìœ¼ë¡œ ì¬ì‹œë„
            const newToken = authService.getAccessToken();
            if (newToken) {
              headers.Authorization = `Bearer ${newToken}`;
              const retryResponse = await fetch(`${this.baseURL}${endpoint}`, {
                credentials: 'include',
                ...options,
                headers,
              });
              
              if (retryResponse.ok) {
                const retryData = await retryResponse.json();
                return { data: retryData };
              }
            }
          } else {
            // í† í° ìƒˆë¡œê³ ì¹¨ ì‹¤íŒ¨ ì‹œ ë¡œê·¸ì•„ì›ƒ ì´ë²¤íŠ¸ ë°œìƒ
            window.dispatchEvent(new CustomEvent('auth:tokenExpired'));
          }
        }
        
        return { error: data.error || data.detail || 'Request failed' };
      }

      return { data };
    } catch (error) {
      logger.error('API request failed:', error);
      return { error: 'Network error' };
    }
  }

  get<T>(endpoint: string): Promise<APIResponse<T>> {
    return this.request<T>(endpoint, { method: 'GET' });
  }

  post<T>(endpoint: string, body?: unknown): Promise<APIResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'POST',
      body: body ? JSON.stringify(body) : undefined,
    });
  }

  put<T>(endpoint: string, body?: unknown): Promise<APIResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'PUT',
      body: body ? JSON.stringify(body) : undefined,
    });
  }

  delete<T>(endpoint: string): Promise<APIResponse<T>> {
    return this.request<T>(endpoint, { method: 'DELETE' });
  }

  patch<T>(endpoint: string, body?: unknown): Promise<APIResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'PATCH',
      body: body ? JSON.stringify(body) : undefined,
    });
  }
}

const apiClient = new APIClient(API_BASE_URL);

// ì¸ì¦ API (authServiceë¡œ ëŒ€ì²´ë˜ì—ˆì§€ë§Œ í˜¸í™˜ì„± ìœ ì§€)
export const authAPI = {
  login: async (credentials: { username: string; password: string }) => {
    try {
      const result = await authService.login(credentials.username, credentials.password);
      return {
        data: { user: result.user },
        error: null
      };
    } catch (error) {
      return {
        data: null,
        error: error instanceof Error ? error.message : 'Login failed'
      };
    }
  },
  
  logout: async () => {
    try {
      const result = await authService.logout();
      return {
        data: result.success ? { message: 'Logout successful' } : null,
        error: result.success ? null : result.error
      };
    } catch (error) {
      return {
        data: null,
        error: error instanceof Error ? error.message : 'Logout failed'
      };
    }
  },
  
  refreshToken: async () => {
    try {
      const result = await authService.refreshAccessToken();
      return {
        data: result.success ? { message: 'Token refreshed' } : null,
        error: result.success ? null : result.error
      };
    } catch (error) {
      return {
        data: null,
        error: error instanceof Error ? error.message : 'Token refresh failed'
      };
    }
  },
  
  verifyToken: async () => {
    try {
      const user = await authService.getCurrentUser();
      return {
        data: user,
        error: null
      };
    } catch (error) {
      return {
        data: null,
        error: error instanceof Error ? error.message : 'Token verification failed'
      };
    }
  },

  // ì¸ì¦ ìƒíƒœ í™•ì¸
  isAuthenticated: () => authService.isAuthenticated(),
  
  // í˜„ì¬ ì‚¬ìš©ì ì •ë³´
  getCurrentUser: async () => {
    try {
      const user = await authService.getCurrentUser();
      return {
        data: user,
        error: null
      };
    } catch (error) {
      return {
        data: null,
        error: error instanceof Error ? error.message : 'Failed to get user info'
      };
    }
  }
};

